.about-card {
    height: 458px;
    width: 432px;
    border: 5.4px solid #003399;
    border-radius: 20px;
    background-image: url(../../assets/sections/common/StarCardBg.png) !important;
    background-color: white;
    background-position: center;
    transform: rotate(2deg);
    display: flex;
    flex-direction: column;
    align-items: center;
}

.about-card__title {
    font-family: "Bagel Fat One", sans-serif;
    font-size: 51px;
    color: #D71118;
    margin-top: 30px;
    text-align: center;
    line-height: 51px;
}

.about-card__subtitle {
    font-family: "Bagel Fat One", sans-serif;
    font-size: 21px;
    color: #D71118;
    margin-top: 10px;
}

.about-card__content {
    font-family: "Bagel Fat One", sans-serif;
    font-size: 16px;
    color: #000;
    margin-top: 10px;
    margin-left: 34px;
    margin-right: 34px;
    text-align: center;
}
